.subContainer {
    margin: 2rem 5rem;
   background-color: rgba(223, 215, 249, 0.2); 
   border-radius: 25px;
   border-top-left-radius: 115px;
   min-height: 407px;
   display: flex;
   flex-direction: column;
   justify-content: center;

}
.subContainer h1 {
    font-family: 'Poppins';
    font-weight: 600;
    font-size: 33pt;
    text-align: center;
}
.submit{
    display: flex;
    align-items: center;
    justify-content: center;
   
}
.submit input{
    width: 40%;
    height: 68px;
    border: none;
    border-radius: 5px; 
}
.submit input[placeholder] {
    padding-left: 2rem;
    font-family: 'Poppins';
    font-size: 14pt;
    font-weight: 400;
    color: #39425D;
}
.btn {
    width: 180px;
    height: 68px;
    background-color: #FF946D;
    border: none;
    border-radius: 7px;
    margin-left: 3rem;
    color: #fff;
    font-family: 'Poppins';
    font-weight: bold;
    font-size: 17pt;
}
.navImg {
    position: absolute;
    transform: translate(1300px, -180px);
}
.plusImg {
    position: absolute;
    transform: translate(1280px, 170px);  
}