.bookContainer {
  display: grid;
  grid-template-columns: 1.5fr 1fr;
  column-gap: 2rem;
  margin: 2rem;
}
.bookHead h4 {
  font-family: "Poppins";
  font-weight: 600;
  font-size: 18pt;
  color: #5e6282;
}
.bookHead h2 {
  font-family: "Volkhov";
  font-weight: bold;
  font-size: 50pt;
  color: #14183e;
}

.details {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: auto;
}
.details h3,
p {
  font-family: "Poppins";
  font-weight: bold;
  font-size: 16pt;
  color: #5e6282;
  margin-left: 2rem;
}
.details p {
  font-weight: 400;
  padding-top: 0;
}
.details h3 {
  padding-bottom: 0;
  margin-bottom: 0;
}
.cardHolder {
  width: 485px;
  min-height: 400px;
  padding: 1rem;
}
.trip {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.holder {
  height: auto;
  width: 321px;
  margin: 2rem 0;
}
.holder img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 7px;
}
.con {
  display: inline-block;
  width: 50px;
  height: auto;
  border-radius: 50%;
  background-color: #f5f5f5;
  margin: 0 2rem;
}
.con img {
  width: 100%;
  height: 10%;
  color: #080809;
  scale: 0.5;
}
.con1 {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
}
h5 {
  font-family: "Poppins";
  font-weight: 500;
  font-size: 18pt;
  color: #080809;
}
p {
  font-family: "Poppins";
  font-weight: 500;
  font-size: 16pt;
  color: #84829a;
}
.con1 p {
  margin-top: 2rem;
  margin-bottom: 2rem;
  margin-right: 12rem;
}
