.reviewsContainer {
        display:grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
        min-width: 1186px;
        min-height: 381px;
        align-items: center;
        margin: 5rem 5rem 0;
}
.txt h4{
    font-family: 'Poppins';
    font-weight: 600;
    font-size: 18pt ;
    color: #5E6282 ;
}
.txt h2{
    font-family: 'Volkhov';
    font-weight: bold;
    font-size: 50pt ;
    color: #14183E ;
}
.slider {
    
    
}
.outer {
    height: 250px;
    width: 704px;
    padding: 2rem ;
    position: absolute;
    transform: translate(-40px, -105px);
    background-color: white;
  border-radius: 15px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

.inner {
    border: 2px solid #e6e6e6;
    background-color: rgba(255, 255, 255, 0.2);
    padding: 2rem ;
    border-radius: 15px;
    height: 250px;
    width: 704px;
}
.outer p, .inner p{
    font-family: "Poppins";
    font-size: 16pt;
    font-weight: 500;
    color: #5E6282;
    margin-bottom: 1rem;
    
}
.outer h4, .inner h4{
    font-family: "Poppins";
    font-size: 18pt;
    font-weight: 600;
    color: #5E6282;
}
.outer h5, .inner h5{
    font-family: "Poppins";
    font-size: 14pt;
    font-weight: 500;
    color: #5E6282;
}
.arrows{ 
    display: flex;
    flex-direction: column;
    position: absolute;
    transform: translate(750px, -200px);
}
.icon {
    margin: 2rem 0;
}