.topSection {
    display: grid;
    grid-template-columns: 2.5fr 2.5fr ;  
    margin: 0 2rem;   
}
.sec1 {
    grid-column: 1/2;
    display: flex;
    flex-direction: column;
    justify-content: left;
    z-index: 5;  
}

.sec1 h4{
    font-family: 'Poppins';
    font-weight: bold;
    font-size: 20pt;
    color: #DF6951;
    padding-top: 4rem;
}
.sec1 h1 {
    position: relative;
    font-family: 'Volkhov';
    font-weight: bold;
    font-size: 84pt;
    color: #181E4B; 
    z-index: 5; 
}
.sec1 p {
    font-family: 'Poppins';
    font-weight: 500;
    font-size: 16pt;
    color: #5E6282;  
}
.sec1 div{   
    justify-content: center;
}
.btn1 {
     margin-right: 1.5rem;
    width: 170px;
    height: 60px;
    background-color: #F1A501;
    border: none;
    border-radius: 5px;
    font-family: 'Sans' ;
    font-weight: 500;
    font-size: 18pt;
    color: #FFF; 
 
}
.btn2 {
    background-color: #FFF;
    border: none;
    border-radius: 5px;
    font-family: 'Poppins' ;
    font-weight: 500;
    font-size: 17pt;
    color: #686D77; 
 
}
.icon {
    display: inline-block;
    color: #DF6951;
    padding-right: 0.5rem;

}
.decImg {
    position: absolute;
    z-index: -1; 
   transform: translateY(80px); 
}

.sec2 {
    grid-column: 2/3;
    position: relative;
}
.trvImg {
   transform: translate(-2rem, -4rem);
}  
.planeImg {
    display: inherit;
    transform: translate(2rem, 8rem);
    
}