.container:hover {
    position: relative;
    z-index: 5;
}
.offers {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 227px;
    min-width: 190px;
    
}

.offers h4 {
    font-family: 'Open Sans';
    font-weight: 600 ;
    font-size: 20pt;
    color: #1E1D4C;
    text-align: center;
}
.offers p {
    font-family: 'Poppins';
    font-weight: 500 ;
    font-size: 16pt;
    color: #5E6282;
    text-align: center;
    margin-top:0 ;
}
.invisible {
    visibility: hidden;
}
.visible {
display: inline;
   position: absolute;
   bottom: 2%;
   z-index: -1;

}
.offers:hover {
    position: absolute;
    background-color: #fff;
    border-radius: 30px;
    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.2);
    transform: translateX(-1px);
    height: 90%;
    margin-left: 3rem ;
    margin-bottom: 2rem;
    transition:none 0.3s ease-in-out ; 

    }
    